import { Box, ToggleButton } from "@rp/react/web/mui/core"
import { Business, HowToReg } from "@rp/react/web/mui/icons"
import { MonochromeToggleButtonGroup } from "../MonochromeToggleButtonGroup"

type Props = {
  value: number
  setValue: (value: number) => void
  size?: "small" | "medium" | "large"
}

const HubFileToggleTabs = ({ value, setValue, size = "medium" }: Props) => {
  return (
    <Box>
      <MonochromeToggleButtonGroup
        sx={{ "& svg": { marginRight: "4px" } }}
        exclusive
        value={value}
        onChange={(_, newValue) => setValue(newValue ?? value)}
        size={size}
      >
        <ToggleButton sx={{ width: 110 }} value={0}>
          <HowToReg />
          My Files
        </ToggleButton>
        <ToggleButton sx={{ width: 160 }} value={1}>
          <Business sx={{ width: 24, height: 24 }} />
          Shared with me
        </ToggleButton>
      </MonochromeToggleButtonGroup>
    </Box>
  )
}

export { HubFileToggleTabs }
